.contacts {
  padding: 20px 0;
  position: relative;
  width: 100%;
  max-width: 1270px;
  height: 900px;
  margin: 0 auto;
  border-left: 1px solid #7e7c7c;
  border-right: 1px solid #7e7c7c;
}
.contacts .title_session_contacts {
  font-size: 16px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1vw;
  padding: 5vw 0;
}
.contacts .title_session_contacts .number_session_contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  background-color: #000;
  color: #fff;
  padding: 6px;
}
.contacts .square {
  background-color: #000;
  width: 30px;
  height: 30px;
}
.contacts .square_one {
  position: absolute;
  right: 0;
  top: 0;
}
.contacts .square_two {
  position: absolute;
  top: 30px;
  right: 30px;
}